[class^="number-slide"],
[class*=" number-slide"] {
    background: grey;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
    color: #fff;
    font-weight: 500;
    height: 300px;
    max-height: 100vh;
}

.number-slide1 {
    background: rgb(64, 175, 255);
    background: linear-gradient(128deg,
            rgba(64, 175, 255, 1) 0%,
            rgba(63, 97, 255, 1) 100%);
}

.number-slide2 {
    background: rgb(255, 75, 64);
    background: linear-gradient(128deg,
            rgba(255, 154, 63, 1) 0%,
            rgba(255, 75, 64, 1) 100%);
}

.number-slide3 {
    background: rgb(182, 255, 64);
    background: linear-gradient(128deg,
            rgba(182, 255, 64, 1) 0%,
            rgba(63, 255, 71, 1) 100%);
    background: linear-gradient(128deg,
            rgba(189, 255, 83, 1) 0%,
            rgba(43, 250, 82, 1) 100%);
}

.number-slide4 {
    background: rgb(64, 255, 242);
    background: linear-gradient(128deg,
            rgba(64, 255, 242, 1) 0%,
            rgba(63, 188, 255, 1) 100%);
}

.number-slide5 {
    background: rgb(255, 64, 156);
    background: linear-gradient(128deg,
            rgba(255, 64, 156, 1) 0%,
            rgba(255, 63, 63, 1) 100%);
}

.number-slide6 {
    background: rgb(64, 76, 255);
    background: linear-gradient(128deg,
            rgba(64, 76, 255, 1) 0%,
            rgba(174, 63, 255, 1) 100%);
}

.navigation-wrapper {
    position: relative;
    display: flex;
    margin-top: 150px;
}

.dots {
    display: flex;
    padding: 10px 0;
    justify-content: center;
}

.dot {
    border: none;
    width: 10px;
    height: 10px;
    background: #c5c5c5;
    border-radius: 50%;
    margin: 0 5px;
    padding: 5px;
    cursor: pointer;
}

.dot:focus {
    outline: none;
}

.dot.active {
    background: #000;
}

.arrow {
    width: 30px;
    height: 30px;
    fill: #fff;
}

.arrow--left {
    left: 5px;
}

.arrow--right {
    left: auto;
    right: 5px;
}

.arrow--disabled g path {
    fill: rgba(255, 255, 255, 0.5);
}

.slider-arrow {
    width: 80px;
    height: 770px;
    background-color: #A53E6F;
    border-radius: 20px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.slider-arrow:first-child {
    margin-right: 30px;
}

.slider-arrow:last-child {
    margin-left: 30px;
}

.slide1 {
    display: flex;
    flex-direction: column;
    height: 770px;
}

.slide1 .nftbox-list:first-child {
    margin-bottom: 25px;
}

.slide1 > div {
    display: flex;
    height: calc(50% - 13px);
}

.slide1 .nftbox {
    /* height: 50%; */
    border-radius: 25px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.slide1 .nftbox1 {
    background-color: #F2AA56;
}

.slide1 .nftbox2 {
    background-color: #6dc16c;
}

.slide1 .nftbox3 {
    background-color: #dfadae;
}

.slide1 .nftbox4 {
    background-color: #f2aa56;
}

.slide1 .nftbox5 {
    background-color: #a482cf;
}

.slide1 .nftbox6 {
    background-color: #1C9FB1;
}

.slide1 .nftbox6 .text {
    font-size: 18px;
    max-width: 376px;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 75px;
    font-weight: bold;
}

.slide1 .nftbox img {
    max-width: 50%;
    width: auto;
    max-height: 80%;
    height: auto;
}

.slide1 .nftbox1, .slide1 .nftbox6 {
    width: 773px;
}

.slide1 .nftbox2, .slide1 .nftbox3, .slide1 .nftbox4, .slide1 .nftbox5 {
    width: 372px;
}

.slide1 .nftbox2, .slide1 .nftbox3 {
    margin-left: 30px;
}

.slide1 .nftbox5, .slide1 .nftbox6 {
    margin-left: 30px;
}

@media only screen and (max-width: 650px) {
    .slider-arrow {
        width: 31px;
        height: 313px;
        border-radius: 10px;
    }

    .slider-arrow:first-child {
        margin-right: 11px;
    }

    .slider-arrow:last-child {
        margin-left: 11px;
    }

    .slide1 {
        height: 313px;
    }

    .slide1 .nftbox-list:first-child {
        margin-bottom: 11px;
    }

    .slide1 > div {
        height: calc(50% - 6px);
    }

    .slide1 .nftbox1, .slide1 .nftbox2, .slide1 .nftbox4, .slide1 .nftbox5 {
        margin-right: 11px;
    }

    .slide1 .nftbox1, .slide1 .nftbox4, .slide1 .nftbox5 {
        display: none;
    }

    .slide1 .nftbox2, .slide1 .nftbox3 {
        width: 50%;
        /* margin-right: 0px; */
    }

    .slide1 .nftbox img {
        max-width: 100%;
    }

    .slide1 .nftbox6 {
        width: 100%;
    }

    .navigation-wrapper {
        margin-top: 100px;
    }

    .slide1 .nftbox6 .text {
        max-width: 170px;
        font-size: 10px;
    }

    .arrow {
        width: 12px;
    }

    .nftbox {
        overflow: hidden;
    }

    /* .slide1 .nftbox1, .slide1 .nftbox2, .slide1 .nftbox4, .slide1 .nftbox5 {
        margin-right: 11px;
    } */
}