.landing {
	.section {
		position: relative;
		line-height: 0px;
		display: flex;
		justify-content: center;

		.logo {
			display: flex;
			align-items: center;
			position: absolute;
			left: 0px;
			top: -10px;

			img {
				width: 177px;
				height: 177px;
			}

			.logotitle {
				font-family: 'Edo', sans-serif;
				font-size: 30px;
				line-height: 40px;
			}
		}

		.banner {
			background-image: url(../../img/banner.png);
			background-position-y: center;
			background-repeat: no-repeat;
			background-size: cover;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.mask1 {
			-webkit-mask-image: url(../../img/mask1.png);
			mask-image: url(../../img/mask1.png);
			-webkit-mask-size: 100%;
			mask-size: 100%;
			-webkit-mask-repeat: no-repeat;
			mask-repeat: no-repeat;
			-webkit-filter: drop-shadow(15px 15px 0 #a53e6f) drop-shadow(-15px -15px 0 #a53e6f);
			filter: drop-shadow(15px 15px 0 #a53e6f) drop-shadow(-15px -15px 0 #a53e6f);
			width: 92%;
		}

		.backgroundtext {
			position: absolute;
			font-size: 139px;
			font-family: 'Edo', sans-serif;
			width: 995px;
			height: 185px;
			line-height: 185px;
			text-align: center;
		}

		.parallax {
			/* The image used */
			background-image: url(../../img/background.png);

			/* Set a specific height */
			min-height: 90%;

			/* Create the parallax scrolling effect */
			background-attachment: fixed;
			background-position-y: 55%;
			background-repeat: no-repeat;
			background-size: cover;
		}
	}

	.section:not(:first-child) {
		margin-left: 60px;
		margin-right: 60px;
	}

	.journey {
		position: absolute;
		justify-content: center;
		display: flex;
		width: 100%;
		bottom: 30px;
	}

	.journey div {
		cursor: pointer;
		position: relative;

	}

	.journey div img {
		position: absolute;
		top: -240px;
		width: 386px;
		left: -30px;
	}

	.journeybtn {
		background-color: #A53E6F;
		border-radius: 50px;
		font-size: 34px;
		line-height: 25px;
		padding: 28px 66px;
		color: white;
		width: auto;
		font-family: 'Monster AG', sans-serif;
	}

	.description {
		font-size: 48px;
		line-height: 58px;
		margin-top: 76px;
		box-shadow: 0px 0px 15px #06060640;
		text-align: center;
		margin-left: 50px;
		margin-right: 50px;
		font-family: "28 days later", Sans-serif;
	}

	.section-1 {
		margin-top: 64px;
		align-items: center;
	}

	.down img {
		width: 41px;
		height: 24px;
	}

	.section-1 .contact-group {
		position: absolute;
		display: flex;
		align-items: center;
		right: 27px;
	}

	.section-2 {
		display: flex;
		align-items: center;

		>img {
			width: 463px;
		}

		.mineral-help {
			margin-left: 30px;

			img {
				display: none;
			}
		}
	}

	.section-3 {
		border: 15px solid #A53E6F;
		border-radius: 25px;
		margin-top: 117px;
		background-image: url("../../img/background.png");
		background-attachment: fixed;
		background-position-y: 75%;
		background-repeat: no-repeat;
		background-size: cover;
		min-height: 400px;
	}

	.section-4 {
		align-items: center;
		margin-top: 100px;

		>img {
			width: 550px;
		}

		.mandrills-help img {
			display: none;
		}
	}


	.section-5 {
		align-items: center;
		margin-top: 100px;

		>img {
			width: 420px;
		}

		.participate-help {
			margin-left: 60px;

			img {
				display: none;
			}
		}
	}



	.section .help .title {
		font-family: '28 Days Later', sans-serif;
		font-size: 48px;
		line-height: 58px;
	}

	.section .help .content {
		margin-top: 40px;
		font-family: 'Segoe-UI', sans-serif;
		font-weight: bold;
		font-size: 24px;
		line-height: 32px;
	}

	@media only screen and (max-width: 1650px) {
		.journeybtn {
			font-size: 25px;
			padding: 25px 30px 25px 30px;
		}

		.journey div img {
			width: 300px;
			top: -180px;
		}

		.journey {
			bottom: 25px;
		}
	}

	@media only screen and (max-width: 1450px) {

		.description,
		.section .help .title {
			font-size: 42px;
			line-height: 40px;
		}

		.section .help .content {
			font-size: 20px;
			margin-top: 30px;
		}

		.section:not(:first-child) {
			margin-left: 50px;
			margin-right: 50px;
		}

		.section-4 img {
			width: 550px;
		}

		.section-5 img {
			width: 380px;
		}
	}

	@media only screen and (max-width: 1200px) {
		.journey {
			bottom: 10px;
		}

		.journeybtn {
			font-size: 20px;
			padding: 15px 20px 15px 20px;
		}

		.journey div img {
			width: 220px;
			top: -120px;
			left: 0px;
		}

		.description,
		.section .help .title {
			font-size: 33px;
		}

		.section .help .content {
			font-size: 16px;
			margin-top: 20px;
		}

		.section:not(:first-child),
		.description {
			margin-left: 40px;
			margin-right: 40px;
		}

		.section-4 img {
			width: 500px;
		}
	}

	@media only screen and (max-width: 900px) {
		.journey {
			bottom: 10px;
		}

		.journeybtn {
			font-size: 15px;
		}

		.journey div img {
			width: 150px;
			top: -90px;
		}

		.description,
		.section .help .title {
			font-size: 25px;
		}

		.section:not(:first-child),
		.description {
			margin-left: 30px;
			margin-right: 30px;
		}

		.section-4 img {
			width: 300px;
		}

		.section-5 img {
			width: 300px;
		}
	}

	@media only screen and (max-width: 650px) {
		.journey {
			bottom: 0px;
		}

		.journeybtn {
			font-size: 7px;
			padding: 8px 10px
		}

		.journey div img {
			width: 100px;
			top: -55px;
		}

		.description,
		.section .help .title {
			font-size: 14px;
			line-height: 25px;
			margin-top: 10px;
		}

		.section:not(:first-child),
		.description {
			margin-left: 20px;
			margin-right: 20px;
		}

		.section-1 {
			margin-top: 7px;

			>a img {
				height: 7.64px;
				width: 12.82px;
			}
		}

		.section-2 {
			// align-items: flex-start;

			.mineral-help {
				margin-left: 0px;
			}

			>img {
				display: none;
			}

			.mineral-header {
				display: flex;
				align-items: center;
				justify-content: space-between;

				img {
					width: 81px;
					display: block;
				}

				.title {
					font-size: 27px;
				}
			}
		}

		.section-3 {
			margin-top: 20px;
			border: 5px solid #A53E6F;
			min-height: 86px;
			border-radius: 10px;
		}

		.section-4 {
			margin-top: 10px;

			>img {
				display: none;
			}

			.mandrills-help {
				.mandrills-header {
					display: flex;
					align-items: center;
					justify-content: space-between;

					img {
						display: block;
						width: 100px;
					}

					.title {
						font-size: 26px;
					}
				}
			}
		}

		.section-5 {
			margin-top: 30px;

			>img {
				display: none;
			}

			.participate-help {
				margin-left: 0px;

				.participate-header {
					display: flex;
					justify-content: space-between;
					align-items: center;

					img {
						display: block;
						width: 81px;
					}

					.title {
						font-size: 27px;
					}
				}
			}
		}
	}
}