/* Global Styles */
@font-face {
	font-family: '28 days later';
	src: url('./font/28-Days-Later.ttf') format('truetype');
}

@font-face {
	font-family: 'Segoe-UI';
	src: url('./font/Segoe-UI.ttf') format('truetype');
}

@font-face {
	font-family: 'Edo';
	src: url('./font/edo.ttf') format('truetype');
}

@font-face {
	font-family: 'Monster AG';
	src: url('./font/Monster\ AG.ttf') format('truetype');
}

:root {
	--primary-color: #17a2b8;
	--dark-color: #343a40;
	--light-color: #f4f4f4;
	--danger-color: #dc3545;
	--success-color: #28a745;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	scroll-behavior: smooth;
}

body {
	font-family: 'Raleway', sans-serif;
	font-size: 1rem;

	background-color: #000;
	color: white;
}

a {
	color: var(--primary-color);
	text-decoration: none;
}

ul {
	list-style: none;
}

img {
	width: 100%;
	display: block;
}

::-webkit-scrollbar {
	width: 20px;
}

::-webkit-scrollbar-thumb {
	background: #A53E6F;
	border-radius: 50px;
}

::-webkit-scrollbar-thumb:hover {
	background: #A53E6F;
}

.elementor-divider-separator {
	display: flex;
	align-items: center;
	margin-top: 45px;
}

.elementor-divider-separator .elementor-divider__element img {
	width: 137px;
}

.elementor-divider-separator:before {
	display: block;
	content: "";
	border-bottom: 0;
	flex-grow: 1;
	border-top: 3px solid #FF049C;
}

.elementor-divider-separator::after {
	display: block;
	content: "";
	border-bottom: 0;
	flex-grow: 1;
	border-top: 3px solid #FF049C;
}

.contact-group a:not(:last-child) {
	margin-right: 33px;
}

.contact-group .discord img {
	width: 30px;
}

.contact-group .medium img {
	width: 40px;
}

.contact-group .twitter img {
	width: 24px;
}

.contact-group .youtube img {
	width: 25px;
}

@media only screen and (max-width: 650px) {
	.contact-group .discord img {
		width: 14px;
	}

	.contact-group .medium img {
		width: 15px;
	}

	.contact-group .twitter img {
		width: 14px;
	}

	.contact-group .youtube img {
		width: 14px;
	}

	.contact-group a:not(:last-child) {
		margin-right: 7px;
	}

	.elementor-divider-separator .elementor-divider__element img {
		width: 60px;
	}

	.elementor-divider-separator:before {
		border-top: 2px solid #FF049C;
		margin-left: -14px !important;
	}

	.elementor-divider-separator::after {
		border-top: 2px solid #FF049C;
		margin-right: -14px !important;
	}

	.elementor-divider-separator {
		margin-top: 20px;
	}
}

.footer {
	.section {
		position: relative;
		line-height: 0px;
		display: flex;
		justify-content: center;
		margin-left: 60px;
		margin-right: 60px;
	}

	.section-6 {
		flex-direction: column;
	}

	.section-6 .footer-section {
		position: absolute;
		left: 0px;
		display: flex;
		flex-direction: column;
		top: -20px;
	}

	.section-6 .footer-section .info {
		font-size: 24px;
		line-height: 29px;
		font-family: 'Segoe-UI', sans-serif;
	}

	.section-6 .footer-section .privacy {
		margin-top: 27px;
	}

	.section-6 .footer-section>a {
		font-family: 'Segoe-UI', sans-serif;
		font-size: 24px;
		line-height: 29px;
		color: #AEAEAE;
	}

	.section-6 .footer-section .contact-group {
		display: flex;
		margin-top: 40px;
		align-items: center;
	}

	.section-6 .logo-name {
		font-size: 30px;
		line-height: 36px;
		margin-top: 30px;
		text-align: center;
		font-family: 'Edo', sans-serif;
	}

	.section-6 .year {
		margin-top: 42px;
		margin-bottom: 20px;
		font-size: 24px;
		line-height: 29px;
		font-family: 'Segoe-UI', sans-serif;
		text-align: center;
	}

	.scrollto {
		cursor: pointer;
		position: fixed;
		width: 95px;
		height: 54px;
		right: 50px;
		bottom: 50px;
	}

	@media only screen and (max-width: 1450px) {
		.section {
			margin-left: 50px;
			margin-right: 50px;
		}
	}

	@media only screen and (max-width: 1200px) {
		.section {
			margin-left: 40px;
			margin-right: 40px;
		}
	}

	@media only screen and (max-width: 900px) {
		.section {
			margin-left: 30px;
			margin-right: 30px;
		}
	}

	@media only screen and (max-width: 650px) {
		.scrollto {
			width: 40px;
			height: auto;
		}

		.section {
			margin-left: 20px;
			margin-right: 20px;

			display: flex;
			align-items: center;

		}

		.section-6 {

			.logo-name,
			.year {
				display: none;
			}

			.footer-section {
				position: relative;
				margin-top: 20px;
				align-items: center;
				margin-bottom: 45px;
				
				.info {
					font-size: 16px;
				}

				.privacy {
					margin-top: 24px;
				}

				a {
					font-size: 16px;
				}

				.contact-group {
					margin-top: 21px;
				}
			}
		}
	}
}